import React, {Component} from 'react';
import {connect} from "react-redux";
import * as Actions from "../../actions";
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

class FormContact extends Component {
    constructor(props) {
        super(props);
        this.state = {
            txtName:'',
            txtEmail:'',
            txtNoidung:''
        }
    }

    isChange = (event) => {
        let name = event.target.name;
        let value = event.target.value;
        this.setState({
            [name]:value
        });
    };

    getTypeNotification = (type) => {
        switch (type) {
            case 'SUCCESS' :
                return toast.success('Gửi Liên hệ thành công!', {
                    position: "top-right",
                    autoClose: 5000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                });
            case 'WARNING' :
                return toast.warning('Thông tin gửi liên hệ không hợp lệ!', {
                    position: "top-right",
                    autoClose: 5000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                });
            case 'ERROR':
            default:
                return toast.error('Gửi liên hệ thất bại!', {
                    position: "top-right",
                    autoClose: 5000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                });
        }
    };

    showNotification = () => {
        if (this.props.data || this.props.error) {
            if (this.props.data && !this.props.data.error) {
                this.getTypeNotification('SUCCESS');
            } else {
                this.getTypeNotification('ERROR');
            }
        } else {
            return;
        }
    };

    handleClick = () => {
        //console.log(JSON.stringify(this.state));
        if (this.state.txtName.length === 0 || this.state.txtEmail.length === 0 || this.state.txtNoidung.length === 0) {
            this.getTypeNotification('WARNING');
        } else {
            this.props.sendData(this.state);
        }
    };

    render() {
        return (
            <div className="contact-form spad">
                <div className="container">
                    <div className="row">
                        <div className="col-lg-12">
                            <div className="contact__form__title">
                                <h2>Liên hệ</h2>
                            </div>
                        </div>
                    </div>
                    <ToastContainer />
                    <form>
                        {this.showNotification()}
                        <div className="row">
                            <div className="col-lg-6 col-md-6">
                                <input type="text" onChange={(e) => this.isChange(e)} name="txtName" placeholder="Họ tên" />
                            </div>
                            <div className="col-lg-6 col-md-6">
                                <input type="text" onChange={(e) => this.isChange(e)} name="txtEmail" placeholder="Email" />
                            </div>
                            <div className="col-lg-12 text-center">
                                <textarea name="txtNoidung" onChange={(e) => this.isChange(e)} placeholder="Nội dung" />
                                <button type="reset" onClick={() => this.handleClick()} className="site-btn">Gửi</button>
                            </div>
                        </div>
                    </form>
                </div>
            </div>
        );
    }
}
const mapStateToProps = state => {
    return {
        fetching: state.payloadContact.fetching,
        params: state.payloadContact.params,
        data: state.payloadContact.data,
        error: state.payloadContact.error
    };
};

const mapDispatchToProps = dispatch => {
    return {
        sendData: (params) => dispatch(Actions.contactAction.sendRequestContact(params))
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(FormContact);